export default {
	data() {
		return {
			// 当前样式
			target: 0,
			// 当前item位置
			thisindex: 0,
			//动态的数据
			trends:[{user_img:"http://pic2.sc.chinaz.com/Files/pic/pic9/202002/hpic2119_s.jpg",user_name:"wky"},{}],
		};
	},
	methods: {
		// 切换触发的事件
		toggle(e) {
			let index = e.detail.current
			this.target = index
		},
		// 点击nav控制下面的展示
		setIndex(e) {
			let index = e.currentTarget.dataset.index
			this.thisindex = index
		},
	}
}
